.tuesday {
  text-align: center;
}

.todoList {
  width: 400px;
  margin: 0 auto;
}

.todoList-task{
  text-align: left;
}

.todoList > div {
  padding: 10px;
}

.filter-active {
  background-color: blue;
}

.error {
  border-color: red;
}

.todoList-task.done {
  opacity: 0.5;
}